  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

body {
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  font-size: 22px;
  line-height: 34px;
  font-weight: normal; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-header {
  background-color: #191919;
  background-image: url("../img/hero-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .main-header {
      background-image: url("../img/hero-desktop@2x.jpg"); } }

.main-header__wrapper {
  max-width: 880px;
  margin: 0 auto; }

.main-header__nav {
  padding-top: 17px;
  margin-bottom: 256px; }

@media (max-width: 1023px) {
  .main-header {
    background: none; }
  .main-header__wrapper {
    width: 100%;
    max-width: 100%; }
  .main-header__nav {
    padding-top: 0;
    margin-bottom: 0; } }

.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.nav__item a {
  font-size: 16px;
  font-weight: 300;
  line-height: 32px;
  color: #ffffff;
  opacity: 0.7; }

.nav__toggle {
  display: none;
  z-index: 3; }

@media (max-width: 1023px) {
  .nav__list {
    display: none;
    flex-direction: column;
    padding: 0;
    padding-top: 100px;
    padding-bottom: 140px; }
  .nav--opened .nav__list {
    display: block;
    position: absolute;
    width: 100%;
    background-color: #ffffff;
    z-index: 2; }
  .nav--opened .nav__toggle {
    background-color: #ffffff;
    background-image: url("../img/icon-menu-close.svg"); }
  .nav--nojs .nav__list {
    display: block; }
  .nav--nojs .nav__toggle {
    background-color: #ffffff;
    background-image: url("../img/icon-menu-close.svg"); }
  .nav__item a {
    display: block;
    position: relative;
    padding-top: 17px;
    padding-bottom: 17px;
    padding-left: 16px;
    margin-bottom: 28px;
    background-color: #f6f6f6;
    font-size: 20px;
    line-height: 28px;
    color: #48494d; }
  .nav__item:last-child a {
    margin-bottom: 0; }
  .nav__item a::after {
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    background-image: url("../img/icon-arrow.svg");
    background-repeat: no-repeat;
    background-position: center; }
  .nav__toggle {
    display: block;
    position: absolute;
    right: 0;
    width: 50px;
    height: 50px;
    background-image: url("../img/icon-menu-open.svg");
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    background-color: #0ad9c6; } }

.hero {
  display: flex;
  justify-content: space-between;
  padding-bottom: 240px; }

.hero__description {
  width: 43%; }

.hero__description-logo {
  margin-bottom: 22px;
  width: 67px;
  height: 20px; }

.hero__description-title {
  margin: 0;
  margin-bottom: 22px;
  font-size: 46px;
  font-weight: bold;
  line-height: 64px;
  letter-spacing: -0.005em;
  color: #ffffff; }

.hero__description-text {
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-weight: 300;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: #ffffff;
  opacity: 0.8; }

.hero__request {
  width: 37%; }

.hero__request-text {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  padding-top: 4px;
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  letter-spacing: -0.005em;
  text-align: center;
  color: #ffffff; }

@media (max-width: 1023px) {
  .hero {
    flex-direction: column;
    align-items: center;
    padding-bottom: 0; }
  .hero__description {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #191919;
    background-image: url("../img/hero-tablet.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    text-align: center; }
  .hero__description-logo {
    padding-top: 25px;
    padding-left: 20px;
    margin-bottom: 80px; }
  .hero__description-logo-link {
    align-self: flex-start; }
  .hero__description-title {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 42px; }
  .hero__description-text {
    width: 56%;
    margin-bottom: 80px;
    font-size: 18px;
    line-height: 32px; }
  .hero__request {
    width: 42%;
    padding-top: 66px; }
  .hero__request-text {
    margin-bottom: 30px;
    color: #000000;
    font-size: 18px; } }

@media (max-width: 767px) {
  .hero__description-text,
  .hero__description-title {
    width: 90%; }
  .hero__description {
    background-image: url("../img/hero-mobile.jpg"); }
  .hero__description-text {
    margin-bottom: 70px; }
  .hero__description-logo {
    padding-left: 17px;
    margin-bottom: 66px; }
  .hero__request {
    width: 90%; }
  .hero__request-text {
    margin-bottom: 20px; } }

.form__input {
  box-sizing: border-box;
  margin-bottom: 20px;
  padding-left: 28px;
  width: 100%;
  height: 48px;
  font: inherit;
  border: 1px solid #c9ccd4;
  border-radius: 6px; }

.form__button {
  box-sizing: border-box;
  min-height: 50px;
  width: 100%;
  font: inherit;
  font-weight: 600;
  font-size: 15px;
  line-height: 14px;
  text-align: center;
  background-color: #0ad9c6;
  border: none;
  border-radius: 6px; }

.form__input::placeholder {
  font-size: 15px;
  line-height: 48px;
  color: #1c1c1c;
  opacity: 0.5; }

@media (max-width: 1023px) {
  .form__input {
    padding-left: 25px; } }

.about-us {
  max-width: 1166px;
  margin: 0 auto;
  padding: 0 4%;
  padding-top: 150px;
  padding-bottom: 150px;
  text-align: center; }

.about-us__title {
  margin: 0;
  margin-bottom: 50px;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  font-weight: 600;
  color: #1f1f1f; }

@media (max-width: 1023px) {
  .about-us {
    padding-top: 72px;
    padding-right: 12%;
    padding-left: 12%; }
  .about-us__title {
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 41px; } }

@media (max-width: 767px) {
  .about-us {
    padding-right: 5%;
    padding-left: 4%; } }

.facts {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between; }

.facts__item {
  width: 22%;
  position: relative;
  letter-spacing: -0.5px; }

.facts__item-number {
  margin-bottom: 14px;
  display: block;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: -1px;
  color: #0ad9c6; }

.facts__item-description {
  margin: 0; }

@media (max-width: 1023px) {
  .facts {
    flex-wrap: wrap;
    justify-content: space-around; }
  .facts__item {
    width: 44%;
    margin-bottom: 44px; } }

@media (max-width: 767px) {
  .facts {
    flex-direction: column; }
  .facts__item {
    width: 100%;
    font-size: 18px;
    line-height: 34px; } }

.bicycle-types {
  background-color: #f6f7f9; }

.bicycle-types__wrapper {
  max-width: 1126px;
  margin: 0 auto;
  padding: 0 6%;
  padding-top: 154px;
  text-align: center; }

.bicycle-types__title {
  margin: 0;
  margin-bottom: 50px;
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  letter-spacing: -0.005em;
  color: #1f1f1f; }

.bicycle-types__list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: space-between; }

.bicycle-types__item:not(:last-child) {
  margin-right: 3%; }

.bicycle-types__item:nth-child(1) {
  padding-top: 10px; }

.bicycle-types__item .bicycle-types__image-wrapper::before {
  position: absolute;
  content: "";
