.bicycle-types__item:nth-child(1) .bicycle-types__image-wrapper::before {
  width: 180px;
  height: 180px;
  border: 27px solid #0ad9c6;
  border-radius: 50%;
  left: -60px;
  top: 154px; }

.bicycle-types__item:nth-child(2) {
  padding-top: 40px; }

.bicycle-types__item:nth-child(2) .bicycle-types__image-wrapper::before {
  left: -15px;
  top: -15px;
  width: 37px;
  height: 37px;
  background-color: #131314;
  z-index: 3; }

.bicycle-types__item:nth-child(3) .bicycle-types__image-wrapper::before {
  top: -34px;
  right: -34px;
  width: 95px;
  height: 95px;
  border: 18px solid #0ad9c6;
  border-radius: 50%;
  z-index: 3; }

.bicycle-types__item:nth-child(2) .bicycle-types__image-wrapper::after {
  position: absolute;
  content: "";
